@import "variables";
@import "mixins";
@import url(../fonts/font.css);
html {
    scroll-behavior: smooth;
}
body {
    padding: 0;
    margin: 0;
    direction: rtl;
    overflow-x: hidden !important;
    font-size: 14px;
    font-family: "main-font";
    line-height: unset !important;
    width: 100% !important;
    min-width: 300px;
    color: #000;
    background-color: #fff;
    * {
        box-sizing: border-box !important;
        margin: 0;
        padding: 0;
    }
    a {
        text-decoration: none !important;
    }
}

/* universal styles */
#root {
    @include flexMixin(column, flex-start, center);
}
.mm-width{
    max-width: 1300px;
}
.bgc-wrapper{
    background-image: url("../images/backgroudn-main.svg");
    background-position: center center;
    background-size: cover;
    background-attachment: fixed;
    width: 100%;
    @include flexMixin(column,flex-start,center);
    padding: 32px 0 100px;
}
a{
    color: unset;
}
@keyframes spinMf {
    from{
        transform : rotate(0deg);
    }
    to{
        transform: rotate(360deg)
    }
}
.inside-loader{
    @include flexMixin(row,center,center);
    svg{
        animation: spinMf 1s ease-in-out 0s infinite normal;
    }
}
img {
    max-width: 100%;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input[type=number] {
    -moz-appearance: textfield;
}
/* universal styles */

/* error component */
.error-wrapper{
    width: 400px;
    height: 100px;
    border: 2px solid $redColor;
    box-shadow: $boxShadow;
    background-color: #fff;
    position: fixed;
    @include flexMixin(row,center,center);
    top: 16px;
    padding: 10px 5px;
    left: clac( 50 % - 200 px );
    color: $redColor;
    font-size: 20px;
    border-radius: 8px;
    transition: $transition;
    text-align: center;
    z-index : 999;
}
.err-show{
    top : -300px;
}
/* error component */

/* footer */
.main-footer{
    width: 100%;
    @include flexMixin(column,flex-start,center);
    gap: 20px;
    position: relative;
    .icons-wrapper{
        @include flexMixin(row,center,center);
        gap: 30px;
        position: absolute;
        top: -50px;
        img{
            width: 30px;
            transition: $transition;
        }
    }
    .gradient-signature{
        width: 100%;
        margin: 0;
        @include flexMixin(row,center,center);
        height: 80px;
        background: #5A85CB;
        color: #fff;
        font-size: 16px;
    }
    .icon{
        width: 40px;
        border: 0px solid $mainColor;
        transition: $transition;
        @include flexMixin(row,center,center);
        height: 40px;
        border-radius: 50% 0% 50% 50%;
        img{
            transition: $transition;
        }
        &:hover{
            // border-radius: 50% 0% 50% 50%;
            // border: 2px solid $mainColor;
            img{
                transform: scale(1.5) rotate(360deg);
                transition: all ease-in-out 0.5s;
                // width: 20px;
            }
        }
    }

}

.new-footer{
    width: 100%;
    @include flexMixin(column,flex-start,center);
    .logo-wrapper{
        width: calc(100% - 70px);
        border-radius: 16px;
        padding: 10px 15px 40px;
        background-color: #5A85CB;
        position: relative;
        z-index: 2;
    }
    .footer-content{
        width: 100%;
        position: relative;
        @include flexMixin(row,center,center);
    }
    .light-blue{
        position: absolute;
        width: 100%;
        height: 300px;
        background: rgb(213, 244, 255);
        background: linear-gradient(180deg, rgb(213, 244, 255) 0%,rgb(213, 244, 255) 50%,#fff 100%);
        right: 0;
        top: -70px;
        z-index: 3;
        transform: rotate(4deg);
        border-radius: 80px 32px 5px 5px;
        z-index: 3;
    }
    .main-content{
        position: relative;
        z-index: 4;
        width: 100%;
        @include flexMixin(row,space-between,flex-start);
    }
    .get-gift{
        font-size: 14px;
        @include flexMixin(column,flex-start,flex-start);
        gap: 10px;
        p{
            font-size: 12px;
            text-align: justify;
            max-width: 300px;
        }
        input{
            width: 100%;
            height: 42px;
            border-radius: 8px;
            border: 2px solid $mainColor;
            outline: 0px solid $mainColor;
            padding: 0 10px;

        }
    }
    .ul-type{
        @include flexMixin(column,flex-start,flex-start);
        gap: 10px;
    }
    .ul-title{
        font-size: 12px;
    }
    ul{
        li{
            list-style: none;
            font-size: 14px;
            line-height: 32px;
            font-weight: bold;
        }
        a{
            transition: $transition;
            display: block;
            &:hover{
                transform: scale(1.1);
                color: $mainColor;
            }
        }
    }
    .icon-img-map-col{
        @include flexMixin(column,flex-start,flex-end);
        gap: 33px;
    }
    .icons{
        @include flexMixin(row,flex-end,center);
        gap: 10px;
        img{
            width: 20px;
            transition: $transition;
            &:hover{
                transform: scale(1.2) rotate(360deg);
            }
        }
    }
    .map-img{
        @include flexMixin(row,flex-end,center);
        gap: 24px;
    }
    .map{
        width: 180px;
        height: 133px;
        background-color: #d9d9d9;
    }
}
/* footer */
/* header */
.main-header{
    @include flexMixin(row,space-between,center);
    width: 100%;
    padding: 11px 20px;
    border-bottom: 2px solid #d9d9d9;
    color: $mainColor;
    margin: 0 0 20px;
    .logo-wrapper{
        width: 181px;
    }
    .cart-name-wrapper{
        @include flexMixin(row,center,center);
        gap: 15px;
        img{
            width: 27px;
        }
    }
    .cart-icon{
        @include flexMixin(row,center,center);
    }
    .user-name-wrapper{
        color: $mainColor;
        @include flexMixin(row,center,center);
        gap: 5px;
        min-width: 120px;
        height: 50px;
        border: 2px solid $mainColor;
        border-radius: 10px;
        font-size: 16px;
        transition: $transition;
        &:hover{
            color: #fff;
            background-color: $mainColor;
            img{
                filter: brightness(0) invert(1);
            }
        }
    }
    .menu-icon{
        display: none;
    }
    .reponsive-icon-for-login{
        display: none;
    }
}
/* header */
/* side bar */
.side-bar-wrapper{
    flex : 0 0 96px;
    height: 630px;
    padding: 20px 0 10px;
    @include flexMixin(column,space-between,center);
    background-color: $mainColor;
    color: #fff;
    border-radius: 8px;
    .items{
        @include flexMixin(column,flex-start,center);
        width: 100%;
        gap: 15px;
    }
    .side-bar-item{
        @include flexMixin(column,center,center);
        gap: 5px;
        font-size: 12px;
        transition: $transition;
        width: 72px;
        height: 72px;
        letter-spacing: -1px;
        &:hover{
            background-color: rgb(28, 97, 170);
            border-radius: 8px;
            box-shadow: $boxShadow;
            transform: translateY(-2px);
        }
    }
    .active{
        background-color: #fff;
        border-radius: 8px;
        color: $mainColor;
        box-shadow: $boxShadow;
        &:hover {
            background-color: #fff;
        }
    }
    .setting{
        img{
            transition: $transition;
            &:hover{
                transform: rotate(360deg) scale(1.5);
                transition: all linear 0.5s;
            }
        }
    }
}
