/* courses */
@keyframes shake {
    0%{
        transform: rotate(10deg);
    }
    50%{
        transform: rotate(-10deg);
    }
    100%{
        transform: rotate(0);
    }
}
@keyframes fade {
    from{
        // opacity: 0.7;
        background-color: #fff018;
        scale: (1);
    }
    to{
        // opacity: 1;
        background-color: #FFAF18;
        transform: scale(1.3);
    }
}
.courses-section{
    .main-content{
        width: 100%;
        @include flexMixin(row, flex-start, flex-start);
        gap: 60px;
    }
    .left-col{
        @include flexMixin(column,space-between,center);
        width: 100%;
    }
    .courses-wrapper{
        display: grid;
        grid-template-columns: repeat(4,1fr);
        column-gap: 40px;
        row-gap: 40px;
        margin: 0 0 40px;
    }
    .course{
        border-radius: 8px;
        @include flexMixin(column,space-between,center);
        padding: 5px 5px 20px;
        background-color: #fff;
        width: 100%;
        gap:15px;
        box-shadow: $boxShadow;
        transition: $transition;
        opacity: 1;
        transform: scale(1);
        img{
            border-radius: 8px;
        }
        .enter-class{
            @include styler(#fff,$redColor,16px,8px);
            height: 50px;
            width: 140px;
            @include flexMixin(row,center,center);
            transition: $transition;
            border: 2px solid $redColor;
            &:hover{
                color: $redColor;
                background-color: #fff;
                animation: shake .2s ease-in-out 0s 2 alternate-reverse;
            }
            &::before{
                animation: fade 1s linear .5s infinite alternate;
            }
        }
        .recorded-sessions{
            @include flexMixin(row, center, center);
            @include styler(#fff, $mainColor, 16px, 8px);
            height: 50px;
            width: 155px;
            border: 2px solid $mainColor;
            &:hover {
                color: $mainColor;
                background-color: #fff;
                animation: shake .2s ease-in-out 0s 2 alternate-reverse;
            }
        }
        &:hover{
            box-shadow: $boxShadow_reverse;
            transform: translateY(-5px);
        }
    }
    .animate-course{
       opacity: 0;
       transform: scale(1.3);
    }
    .active{
        .enter-class{
            position: relative;
            &::before{
                width: 15px;
                height: 15px;
                border-radius: 50%;
                background-color: $yellowColor;
                position: absolute;
                top: -5px;
                right: -5px;
                content: "";
                transition: $transition;
            }
        }
    }
    .no-courses{
        width: 100%;
        @include flexMixin(column,flex-start,center);
        padding: 20px;
    }
    .title-for-empty{
        font-size: 24px;
        line-height: 35px;
        margin: 60px 0 100px;
