        top: 84px;
        right: 0;
        height:calc(100vh - 80px);
        z-index: 99;
        @include flexMixin(row,flex-start,flex-start);
        transition: $transition;
        transform: scale(1);
        .side-bar-wrapper{
            @include flexMixin(column,space-between,center);
            padding:40px 0px 20px;
            height: 100%;
            border-radius: 8px 0 0 0;
            .items{
                gap: 30px;
            }
        }
    }
    .animate-menu{
        transform: scale(0);
    }
    .side-bar-wrapper{
        display: none;
    }
    .mm-width{
        padding: 20px 15px;
    }
    .profile-section{
        .profile-wrapper{
            @include flexMixin(column,flex-start,center);
        }
        .mian-content{
            @include flexMixin(column, flex-start, center);
        }
        .forms-wrapper{
            @include flexMixin(column, flex-start, center);
            gap: 20px;
        }
        .col{
            flex: 0 0 100%;
            width: 100%;
            gap: 20px;
        }
        .options{
            grid-template-columns: repeat(3,1fr);
        }
    }
    .dashboard{
        .slider-calender{
            @include flexMixin(column,flex-start,center);
        }
        .events-wrapper{
            @include flexMixin(column,flex-start,center);
            gap: 20px;
        }
        .counter-wrapper{
            width: 100%;
            flex: 0 0 160px;
        }
        .todays-classes{
            width: 100%;
        }
        .date-and-counter{
            @include flexMixin(column, flex-start, center);
            width: 100%;
            .counter-wrapper,.date{
                width: 100%;
            }
        }
        .new-calender-wraper {
            .new-cal-content{
                width: 100%;
                gap: 14px;
            }
            .days-wrapper{
                justify-content : flex-start;
            }
            .time-class{
                gap: 10px;
                flex-wrap: nowrap;
            }
            .days-wrapper{
                gap: 2px;
            }
            .day{
                font-size: 14px;
                width: 54px;
                height: 32px;
            }
            .class-name{
                font-size: 18px;
            }
            .teachers-name{
                font-size: 14px;
            }
        }
        .drag-place{
            // background-color: rgba(0,0,0,.3);
            width: 4000px;
        }
        .orange-wrapper{
            width: 100%;
        }
        .slide{
            flex:0 0 100%;
            width: unset;
        }
        .ins-sentence{
            flex: 0 0 100%;
        }
        .calender-video{
            @include flexMixin(column,flex-start,center);
        }
        .question-box{
            width: calc(100% - 30px);
            padding: 20px 10px;
        }
        .pop-up-title{
            font-size: 14px;
        }
        .pop-up-header{
            padding: 0;
        }
        .question-text{
            font-size: 16px;
            line-height: 30px;
        }
    }
    .courses-section{
        .courses-wrapper{
            grid-template-columns: repeat(2,1fr);
            column-gap: 20px;
            row-gap: 20px;
        }
    }
    .courses-pop-up-wrapper{
        .main-content{
            width: 200px;
            height: auto;
            font-size: 13px;
            padding: 50px 10px;
        }
    }
    .topics-wrapper-section{
        .main-content{
            grid-template-columns: repeat(2,1fr);
        }
        .topic{
            .title{
                font-size: 20px;
            }
            .desc{
                font-size: 16px;
            }
            .price{
                font-size: 14px;
            }
            .buy-topic{
                font-size: 14px;
                height: 30px;
            }
        }
    }
    .finance-section{
        .finance-wrapper{
            @include flexMixin(column,flex-start,center);
        }
        .main-content{
            margin: 0;
        }
        .discounts-btn{
            flex: 0 0 200px;
            width: 200px;
            max-height: 60px;
        }
    }
    .login-wrapper-section ,.singup-wrapper-section {
        .login-wrapper , .signup-wrapper{
            @include flexMixin(column,flex-start,center);
            gap: 40px;
        }
        .hero_img{
            padding: 50px;
            flex: 0 0 100%;
            width: 100%;
        }
        .forms-wrapper{
            padding: 0 0 50px;
        }
    }
    .single-session{
       .single-text,.single-details{
        padding: 0 10px;
       }
        .no-jozve-wrapper{
            width: 100%;
            gap: 5px;
            font-size: 14px;
        }
    }
    .under-construction-section{ 
        height: 100vh;
        .main-content{
            h1{
                width: 100%;
                font-size: 20px;
            }
        }
    }
    .free-courses-wrapper {
        .main-content{
            gap: 40px;
        }
        .boxes-place{
            width: 100%;
            align-items: center;
        }
        .subjects{
            @include flexMixin(column,flex-start,center);
            flex-wrap: wrap;
            align-content:center;
        }.fields,.kinds{
            display: grid;
            grid-template-columns: repeat(2,1fr);
            row-gap: 20px;
            column-gap: 10px;
        }
        .f-courses-wrapper{
            grid-template-columns: repeat(2, 1fr);
        }
    }
    .shop-wrapper-section{
        .slider-wrapper{
            padding: 0;
        }
        .category{
            max-width: 96px;
            font-size: 16px;
        }
        .filters-wrapper{
            @include flexMixin(column,flex-start,center);
            gap: 20px;
        }
        .filter{
            width: 90%;
            align-items: center;
        }
        .filter-title{
            width: 100%;
        }
        .main-filter{
            // width: 100%;
            max-width: 100%;
        }
        .filter-items-wrapper{
            max-width: 100%;
        }
    }
    .main-footer{
        .gradient-signature{
            font-size: 12px;
        }
    }
    .new-footer{
        overflow-x: hidden;
        .main-content{
            flex-direction: column;
            padding: 20px 15px;
            gap: 20px;
        }
        .light-blue{
            top: -90px;
            width: 100%;
            border-radius: 40px;
            transform: rotate(0deg);
        }
        .logo-wrapper{
            width: 100%;
            max-width: unset;
            padding: 20px 10px 100px;
            height: unset;
            @include flexMixin(row,center,center);
        }
        .icon-img-map-col{
            width: 100%;
            align-items: center;
        }
    }
    .recorded-sessions-section{
        .recorded-session-item{
            width: 100%;
        }
    }
}


